.PostList {
  margin: 0;
  padding: 24px;
  max-width: 800px;

  display: flex;
  flex-direction: column;
  gap: 48px;

  list-style: none;
  background: linear-gradient( 269.8deg, rgba(165, 165, 165, 0.3) 1.49%,
  rgba(237, 237, 237, 0.3) 50.36%, rgba(165, 165, 165, 0.3) 98.53%);
  backdrop-filter: blur(100px);
  border-radius: 32px;
}
